
import { defineComponent } from "vue";
import { mapActions, mapGetters } from "vuex";
import Todo from "./Todo.vue";

export default defineComponent({
  name: "Todos",
  components: {
    Todo,
  },
  computed: {
    ...mapGetters(["allTodos", "loading"]),
  },
  methods: {
    ...mapActions(["fetchAllToDos"]),
  },

  created() {
    this.fetchAllToDos();
  },
});
