
import { defineComponent } from "vue";
import { mapActions } from "vuex";

export default defineComponent({
  name: "AddToDo",
  data() {
    return {
      newTodoTitle: "",
    };
  },
  methods: {
    ...mapActions(["addTodo"]),
    addNewTodo(): void {
      if (this.newTodoTitle.length > 0) {
        this.addTodo(this.newTodoTitle);
        this.newTodoTitle = "";
      }
    },
  },
});
